
.Circuits-div {
    margin: 0 auto;
    font-size: 1.5rem;
    width:100vw;
    
    /* padding: 0 5em; */
   text-align: center;
   padding-bottom:5em;
   font-family: "Saira Stencil One", cursive;
   background: rgba(212, 11, 11, 0.7);
  
   background: rgba(238, 146, 26, 0.5);
   background-repeat: no-repeat;
   background-size: cover;
   height:100%;
}


  .Circuits-h1 {
    width: 100vw;
    text-align: center;
    color:rgb(241, 237, 237);
    font-weight: bold;
    /* font-family: "Keania One", cursive; */
    text-transform: uppercase;
   
   /* margin: 0 auto; */
    font-family: "Saira Stencil One", cursive;
     
  }

  .Circuits-cards-div{
      
    justify-items:center ;
      /* padding-left:5em; */
      /* margin: 0 auto; */
     
     
    display: grid;
  
    grid-template-columns: repeat( auto-fit, minmax(270px, 340px) );
  }



  @media (max-width: 400px) {
    .Circuits-div{
      background: rgb(29, 28, 28);
    }
    .Circuits-h1{
        font-size:1.8rem;
        
    }


    }